.clock{
	font-family: 'Sifonn';
	letter-spacing: 12px;
	font-size: 28px;
}

.second-bar{
	-webkit-animation: off 1s linear both infinite;
	        animation: off 1s linear both infinite;
}

@-webkit-keyframes off{
	0%{
		opacity: 1;
	}
	100%{
		opacity: 0;
	}
}

@keyframes off{
	0%{
		opacity: 1;
	}
	100%{
		opacity: 0;
	}
}


