.App {
  text-align: center;
	
}

.date-time{
	margin:auto;
	height: 170px;
	width: 100%;
	position:fixed;
	top:0;
	bottom:0;
	left:0;
	right:0;
}

.location-weather{
  position: fixed;
  left: 50%;
  bottom: 80px;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin: 0 auto;
}

.todo{
  position: fixed;
  left: 5%;
  top: 3%;
  margin: 0 auto;
}

.news{
  position: fixed;
  left: 80%;
  top: 6%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin: 0 auto;
}

.newsresults{
  position: fixed;
  left: 80%;
  top: 28%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin: 0 auto;
}


