html {height:100%;}
body {
  margin: 0;
  padding: 0;
  color:aliceblue;
}

#root{
	-webkit-animation: fadein 2s ease-in;
	        animation: fadein 2s ease-in;
}

@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}


