ul li{
	font-size: 12px;
}

.news{
	text-align: center;
	letter-spacing: 3px;
	font-family: 'Bebas Neue';
	font-size: 23px;
}

a{
	text-decoration: none;
	color:inherit;
}

.newsresults{
	-webkit-animation: fadein 1.5s ease-in;
	        animation: fadein 1.5s ease-in;
	font-family: 'Montserrat Light';
	letter-spacing: 3px;
	text-align: left;
	line-height: 23px;
}

.source{
	font-size: 10px;
	text-align: right;
}

@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}


